.loading, .container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes pulse {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: .25;
    transform: scale(.75);
  }
}

*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: #1f2632;
  font-family: Manrope, sans-serif;
  font-weight: 400;
  line-height: 1.6;
  position: relative;
}

.container {
  max-width: 80rem;
  min-height: 50%;
  text-align: center;
  background-color: #323a49;
  border-radius: 1.5rem;
  flex-direction: column;
  justify-content: space-around;
  padding: 4rem;
  display: flex;
}

@media only screen and (max-width: 37.5em) {
  .container {
    width: calc(100% - 6rem);
    padding: 4rem 2rem;
  }
}

@media only screen and (max-width: 26.25em) {
  .container {
    width: calc(100% - 3rem);
    padding: 4rem 1.5rem;
  }
}

.adviceId {
  color: #52ffa8;
  text-transform: uppercase;
  letter-spacing: 4px;
  font-size: 1.8rem;
}

@media only screen and (max-width: 37.5em) {
  .adviceId {
    font-size: 1.5rem;
  }
}

.adviceContent {
  color: #cee3e9;
  font-size: 3.2rem;
  font-weight: 800;
}

@media only screen and (max-width: 37.5em) {
  .adviceContent {
    font-size: 2.8rem;
  }
}

@media only screen and (max-width: 26.25em) {
  .adviceContent {
    font-size: 2.4rem;
  }
}

.decorationLine {
  margin: 0 auto;
}

.error__message {
  display: none;
}

.loading {
  width: 10rem;
  opacity: 0;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.loading__bubble {
  width: 2rem;
  height: 2rem;
  background-color: #52ffa8;
  border-radius: 50%;
}

.loading__bubble-1 {
  animation: .4s infinite alternate pulse;
}

.loading__bubble-2 {
  animation: .4s .2s infinite alternate pulse;
}

.loading__bubble-3 {
  animation: .4s .4s infinite alternate pulse;
}

.btn {
  width: 8rem;
  height: 8rem;
  cursor: pointer;
  background-color: #52ffa8;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: all .3s;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 50%);
}

.btn:hover {
  box-shadow: 0 0 3rem #52ffa8cc;
}

.btn__icon {
  width: 3rem;
  height: 3rem;
}

@media only screen and (max-width: 26.25em) {
  .btn__icon {
    width: 2.6rem;
    height: 2.6rem;
  }

  .btn {
    width: 7rem;
    height: 7rem;
  }
}

.attribution {
  color: #4e5d73;
  text-align: center;
  padding-top: .5rem;
  font-size: 1.2rem;
  font-weight: 400;
}

.attribution a {
  text-decoration: none;
  transition: all .2s;
}

.attribution a:link, .attribution a:visited {
  color: currentColor;
}

.attribution a:hover, .attribution a:active {
  color: #52ffa8;
  text-shadow: 0 0 .5rem #52ffa8;
}

